* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  letter-spacing: 1.2px;
  background: #000107;
  flex-direction: column;
  font-family: Roboto Mono, monospace;
  font-size: 1em;
  line-height: 1;
  display: flex;
}

h1, h2, h3, h4, h5 {
  letter-spacing: .4px;
}

a {
  color: #1e90ff;
  text-decoration: none;
}

a:hover {
  color: #00bfff;
  text-decoration: none;
}

.hidden {
  display: none;
}

.gameover {
  color: #ff0107 !important;
}

#header {
  height: 60px;
  min-height: 60px;
}

#main {
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

#board {
  width: 512px;
  margin: auto;
}

.canvas-wrapper {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  justify-content: center;
  padding: 20px;
  display: flex;
}

#canvas {
  cursor: url("circle3.5deea94e.cur") 8 8, auto;
  width: 512px;
  height: 512px;
}

#information {
  color: #f1f7ff;
  flex-flow: wrap;
  justify-content: space-evenly;
  gap: 10px;
  padding: 0 10px;
  display: flex;
}

.stats {
  text-align: center;
  min-width: 80px;
  padding: 5px;
}

.stats-label {
  color: #abb1ba;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 1.2;
}

.stats-value {
  font-size: 24px;
  line-height: 1;
}

#healthbar-wrapper {
  width: 512px;
  height: 55px;
  margin: 0 auto;
}

#healthbar {
  background-color: #f1f7ff;
  width: 0%;
  height: 100%;
}

#settings {
  border: 1px solid #f1f7ff33;
  border-radius: 4px;
  margin: 15px;
  padding: 15px;
}

#settings input {
  color: #f1f7ff;
  background: #f1f7ff1a;
  border: 0;
  border-radius: 4px;
  outline: 0;
  width: 100%;
  padding: 5px;
  font-family: monospace;
  font-size: 24px;
  line-height: 1;
}

#settings button {
  color: #000107;
  cursor: pointer;
  background: #f1f7ff;
  border: 0;
  border-radius: 4px;
  outline: 0;
  margin: 5px;
  padding: 8px 15px;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 1;
  transition: background-color .2s;
}

#settings button:active {
  background-color: #d0d6e0;
}

.params {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.param-cell {
  flex: 1;
  min-width: 120px;
}

.param-label {
  color: #abb1ba;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 1.5;
}

.debug-mode {
  flex-wrap: wrap;
  gap: 5px;
  padding-top: 15px;
  display: flex;
}

@media screen and (width <= 445px) {
  #header {
    height: 60px;
  }

  #board {
    width: 100%;
    max-width: 512px;
  }

  #healthbar-wrapper {
    width: 100%;
    max-width: 512px;
    height: 40px;
    margin: 0 auto;
    padding: 0 10px;
  }

  .canvas-wrapper {
    padding: 10px;
  }

  #canvas {
    width: 100%;
    max-width: 512px;
    height: auto;
  }

  #information {
    padding: 0 10px;
  }

  .stats-label {
    font-size: 10px;
  }

  .stats-value {
    font-size: 20px;
  }

  #settings {
    margin: 10px;
    padding: 10px;
  }

  #settings input, #settings button {
    padding: 6px 12px;
    font-size: 20px;
  }

  .param-cell {
    min-width: 100px;
  }

  .debug-mode {
    flex-direction: column;
  }

  .debug-mode button {
    width: 100%;
  }
}

@media screen and (width >= 446px) and (width <= 1023px) {
  .stats {
    min-width: 90px;
  }

  .stats-label {
    font-size: 14px;
  }

  .stats-value {
    font-size: 22px;
  }
}

@media screen and (width >= 1024px) {
  .canvas-wrapper {
    padding: 20px;
  }

  .stats {
    min-width: 100px;
  }

  .stats-label {
    font-size: 16px;
  }

  .stats-value {
    font-size: 28px;
  }
}
